import React, { useEffect, useMemo, useState } from 'react';
import EntityForm from 'modules/entity/forms';
import NewsFormElements from './NewsFormElements';
import { api, queryBuilder } from 'shared/services';
import { useHistory } from 'react-router-dom';
import get from 'lodash/get';
import { useNotistack, useRouter } from 'shared/hooks';
import { useAppState } from 'shared/state';
import { formatNewsTime } from '../utils';
import dayjs from 'dayjs';

const keywords = [
	{ name: 'Reader First Name', code: '{reader_first_name}' },
	{ name: 'Reader Last Name', code: '{reader_last_name}' },
	{ name: 'Reader Company', code: '{business_company}' },
	{ name: 'Author First Name', code: '{author_first_name}' },
	{ name: 'Author Last Name', code: '{author_last_name}' },
	{ name: 'Author company', code: '{client_company}' }
];

const frequencies = [
	{
		name: 'Daily',
		code: 'DAILY'
	},
	{
		name: 'Weekly',
		code: 'WEEKLY'
	},
	{
		name: 'Monthly',
		code: 'MONTHLY'
	},
	{
		name: 'Yearly',
		code: 'YEARLY'
	},
	{
		name: 'Quarterly',
		code: 'QUARTERLY'
	}
];

const statuses = [
	{
		value: 'ACTIVE',
		label: 'Active'
	},
	{
		label: 'On hold',
		value: 'ON_HOLD'
	}
];

function CreateNewsForm() {
	const [timezone, setTimeZone] = useState({});
	const offset = -new Date().getTimezoneOffset();
	const history = useHistory();
	const { params } = useRouter();
	const campaignId = parseInt((params as { campaignId: string })?.campaignId);
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();

	const defaultStartTime = useMemo(() => dayjs().format(), []);
	const defaultEndTime = useMemo(() => dayjs().format(), []);

	useEffect(() => {
		(async function () {
			try {
				const { data } = await api.request.get(
					queryBuilder(`/timezones/current_timezone/${offset}/`, {})
				);
				setTimeZone(data);
			} catch (err) {
				console.error(err);
			}
		})();
	}, []);

	return (
		<EntityForm.Main
			entity="news"
			name={`${campaignId ? `AllNews-${campaignId}` : 'AllRegularNews'}`}
			url={`/${companyId}/notifications/`}
			primaryKey="id"
			sendAsFormData={false}
			normalizeData={(data: any) => data}
			prependData
			method="post"
			fields={[
				{
					name: 'subject',
					required: true,
					value: '',
					onSubmitKey: ''
				},
				{
					name: 'body',
					required: true,
					value: '',
					onSubmitKey: ''
				},
				{
					name: 'files',
					value: [],
					onSubmitKey: '',
					type: 'object'
				},
				{
					name: 'type',
					value: 'staff',
					onSubmitValue: (value) => (campaignId ? 'campaign' : value),
					onSubmitKey: ''
				},
				{
					name: 'status',
					value: {
						value: 'ACTIVE',
						label: 'Active'
					},
					onSubmitValue: (value) => value.value,
					type: 'object',
					onSubmitKey: ''
				},
				{
					name: 'open_for_cases',
					value: false,
					onSubmitKey: ''
				},
				{
					name: 'frequency',
					value: 'ONE_TIME',
					onSubmitKey: ''
				},
				{
					name: 'timezone',
					onSubmitValue: (value) => ({ id: get(value, 'id') }),
					value: timezone,
					onSubmitKey: '',
					type: 'object'
				},
				{
					name: 'campaign',
					value: null,
					onSubmitValue: () => (campaignId ? Number(campaignId) : undefined),
					onSubmitKey: ''
				},
				{
					name: 'start_time',
					value: defaultStartTime,
					onSubmitValue: (value) => formatNewsTime(value),
					onSubmitKey: '',
					type: 'date',
					customValidation: (value) => {
						return value === 'now';
					}
				},
				{
					name: 'end_time',
					value: defaultEndTime,
					onSubmitValue: (value) => (value ? formatNewsTime(value) : value),
					onSubmitKey: '',
					type: 'date',
					customValidation: (value) => {
						return value === 'now';
					}
				},
				{
					name: 'event_times',
					onSubmitValue: (value, values) => ((values as any).frequency === 'EVENTS' ? value : null),
					onSubmitKey: '',
					type: 'object'
				},
				{
					name: 'department',
					onSubmitValue: (value) => get(value, 'id'),
					onSubmitKey: '',
					type: 'object'
				},
				{
					name: 'sub_department',
					onSubmitValue: (value) => get(value, 'id'),
					onSubmitKey: '',
					type: 'object'
				},
				{
					name: 'staff_departments',
					value: null,
					onSubmitValue: (value) => value?.reduce((acc: any, curr: any) => [...acc, curr.id], []),
					onSubmitKey: '',
					type: 'object'
				},
				{
					name: 'staff_sub_departments',
					value: undefined,
					onSubmitKey: '',
					type: 'object'
				},
				{
					name: 'categories',
					value: null,
					onSubmitValue: (value) => value?.reduce((acc: any, curr: any) => [...acc, curr.id], []),
					onSubmitKey: '',
					type: 'object'
				}
			]}
			onSuccess={(data, resetForm) => {
				resetForm();
				if (campaignId || data?.receiver.campaign) {
					history.push(`/news/campaigns/view/${campaignId}`);
				} else {
					history.push(`/news/news-list`);
				}
			}}
			onError={(error, resetForm): any => {
				const errorText = error?.response?.data?.message || 'Something went wrong';

				showNotification({ message: errorText, variant: 'error' });
			}}
		>
			{({ isSubmitting, values, setFieldValue, errors, setFieldError }) => {
				if (process.env.NODE_ENV === 'development') {
					console.log('news form values', values);
					console.log('errors news create', errors);
				}
				return (
					<NewsFormElements
						isSubmitting={isSubmitting}
						values={values}
						setFieldValue={setFieldValue}
						keywords={keywords}
						frequencies={frequencies}
						statuses={statuses}
						campaignId={campaignId}
						setFieldError={setFieldError}
						errors={errors}
					/>
				);
			}}
		</EntityForm.Main>
	);
}

export default CreateNewsForm;
